import React, { useEffect, useRef, useState } from 'react';
import { v4 as uuidv4 } from 'uuid';

import SimpleBarReact from "simplebar-react";
import "simplebar/src/simplebar.css";
import { ReactComponent as ArrowDown } from '../img/arrowDown.svg';
import { ReactComponent as ArrowUp } from '../img/arrowUp.svg';
import { ReactComponent as Actions } from '../img/actions.svg';
import { ReactComponent as Cross } from '../img/cross.svg';
import { ReactComponent as ActualIcon } from '../img/actualIcon.svg';

import classNames from 'classnames';

import { Data } from '../Types/Data';
import { ResizeableTable } from '../Resizeable';


type Props = {
  data: Data[],
  currentPage: number,
  onPageChange: (pageNumber: number, foundedItems?: Data[]) => void,
  total: Data[],
  isImageView: boolean,
  settingFields: boolean,
  handleOnSort: (type: string, fieldName: string) => void,
}

const tableHeaders = [
  "",
  "",
  "",
  "",
  "..",
  ' .',
  ' .',
  '',
];

export const Table: React.FC<Props> = ({
  data,
  isImageView,
  settingFields,
  handleOnSort,
}) => {
  const [isCheckedHeader, setIsCheckedHeader] = useState<boolean>(false);
  const [headers, setHeaders] = useState<string[]>(tableHeaders);
  const [activeIndex, setActiveIndex] = useState<null | number>(null);

  const swapElements = (array: string[], index1: number, index2: number) => {
    const temp = array[index1];
    array[index1] = array[index2];
    array[index2] = temp;

    const copy = [...array];
    setHeaders(copy);
  };

  useEffect(() => {
    // console.log(headers);
  }, [headers]);

  return (
    <>
      {settingFields ? (
        <ResizeableTable swapElements={swapElements} headers={headers} />
      )
        : (
          <table className='fields__table table'>
            <thead className='table__thead'>
              <>

                {headers.map((header, i) => {
                  if (i === 0) {
                    return (
                      <div className='table__first-part'>
                        <tr className='table__tr'>
                          <th>
                            <label className="container">
                              <input
                                type="checkbox"
                                onChange={() => setIsCheckedHeader(!isCheckedHeader)}
                              />
                              <span className="checkmark"></span>
                            </label>
                          </th>
                        </tr>
                        <tr
                          className='table__tr'
                        >
                          <th
                            onClick={(e) => {
                              if (i !== activeIndex) {
                                setActiveIndex(i);
                              }
                              handleOnSort('string', e.currentTarget.innerText)
                            }}
                          >
                            {header} {i === activeIndex ? (
                              <ArrowDown />
                            ) : (
                              <ArrowUp />
                            )}
                          </th>
                        </tr>
                      </div>
                    )
                  }
                  return (
                    <tr className='table__tr'>
                      <th
                        onClick={(e) => {
                          if (i !== activeIndex) {
                            setActiveIndex(i);
                          }
                          handleOnSort('string', e.currentTarget.innerText)
                        }}
                      >
                        {header} {i === activeIndex ? (
                          <ArrowDown />
                        ) : (
                          <ArrowUp />
                        )}
                      </th>
                    </tr>
                  )
                })}
                <tr className='table__tr'>
                  <th>
                    <Actions />
                  </th>
                </tr>
              </>

              {/* <tr className='table__tr'>
                <th
                  onClick={(e) => {
                    console.log('clicked');
                    handleOnSort('num', e.currentTarget.innerText)
                  }}
                >
                   <ArrowUp />
                </th>
              </tr>
              <tr className='table__tr'>
                <th
                  onClick={(e) => {
                    console.log('clicked');
                    handleOnSort('string', e.currentTarget.innerText)
                  }}
                >
                   <ArrowUp />
                </th>
              </tr>
              <tr className='table__tr'>
                <th
                  onClick={(e) => {
                    console.log('clicked');
                    handleOnSort('string', e.currentTarget.innerText)
                  }}
                >
                   <ArrowUp />
                </th>
              </tr>
              <tr className='table__tr'>
                <th
                  onClick={(e) => {
                    console.log('clicked');
                    handleOnSort('string', e.currentTarget.innerText)
                  }}
                >
                  .. <ArrowUp />
                </th>
              </tr>
              <tr className='table__tr'>
                <th
                  onClick={(e) => {
                    console.log('clicked');
                    handleOnSort('num', e.currentTarget.innerText)
                  }}
                >
                   . <ArrowUp />
                </th>
              </tr>
              <tr className='table__tr'>
                <th
                  onClick={(e) => {
                    console.log('clicked');
                    handleOnSort('num', e.currentTarget.innerText)
                  }}
                >
                   . <ArrowUp />
                </th>
              </tr>
              <tr className='table__tr'>
                <th
                  onClick={(e) => {
                    console.log('clicked');
                    handleOnSort('string', e.currentTarget.innerText)
                  }}
                >
                   <ArrowUp />
                </th>
              </tr>
              <tr className='table__tr'>
                <th>
                  <Actions />
                </th>
              </tr> */}
            </thead>
            <SimpleBarReact style={{
              maxHeight: 695,
            }}
              forceVisible="y"
              autoHide={false}
            >
              <tbody className={classNames(
                'table__tbody',
                {
                  'table__flex-column': isImageView,
                }
              )}>
                {isImageView
                  ? (
                    data.map((item) => {

                      return (
                        <div key={uuidv4()} className='photo__card card'>
                          <div className='card--wrapper'>
                            <div className='card__title'>
                              <div className='card__checkbox'>
                                <label className="container">
                                  {isCheckedHeader ?
                                    (<input defaultChecked={true} type="checkbox" />)
                                    : (<input defaultChecked={false} type="checkbox" />)}
                                  <span className="checkmark"></span>
                                </label>
                              </div>
                              <h2 className='card__title-text'>{item.name}</h2>
                              <div className='card__title-actions'><Actions /></div>
                            </div>
                            <div className='card__under-title'>
                              <p className='card__serial-num'>{item.vendorCode}</p>
                              <p className='card__item-name'>{item.category}</p>
                              <p className='card__prices'>
                                <span>{item.perchasePrice}$</span>
                                <span className='card__price-second'>{item.salePrice}$</span>
                              </p>
                            </div>
                            <div className='card__img--wrapper'>
                              <img className='card__img' src="https://cdn.shopify.com/s/files/1/0554/6747/5144/products/Boleslawiecmiska2.2_195x195@2x.png?v=1632312119" alt="" />
                            </div>
                            <div className="card__footer">
                              <div>
                                <span className={`table__item item-status item-status-${item.status}`}>
                                  <span className={`item-status__wrapper-${item.status}`}>
                                    {item.status === 'red' ? (
                                      <>
                                        
                                        <span className='item-status__icon'>
                                          <Cross />
                                        </span>
                                      </>
                                    ) : (
                                      <>
                                        
                                        <span className='item-status__icon'>
                                          <ActualIcon />
                                        </span>
                                      </>
                                    )}
                                  </span>
                                </span>
                              </div>
                              <div className="card__count">
                                <p className="card__count-p"></p>
                                <p className="card__count-p">.</p>
                              </div>
                            </div>
                          </div>
                        </div>
                      );
                    })
                  )
                  : (
                    <>
                      {data && (
                        data.map((item) => (
                          <tr key={uuidv4()}>
                            <>
                              {headers.map((header, i) => {
                                if (i === 0) {
                                  switch (header) {
                                    case '':
                                      return (
                                        <div className='table__first-part'>
                                          <td>
                                            <label className="container">
                                              {isCheckedHeader ?
                                                (<input defaultChecked={true} type="checkbox" />)
                                                : (<input defaultChecked={false} type="checkbox" />)}
                                              <span className="checkmark"></span>
                                            </label>
                                          </td>
                                          <td style={{ 'width': '110px' }}>{item.name}</td>
                                        </div>
                                      );
                                    case '':
                                      return (
                                        <div className='table__first-part'>
                                          <td>
                                            <label className="container">
                                              {isCheckedHeader ?
                                                (<input defaultChecked={true} type="checkbox" />)
                                                : (<input defaultChecked={false} type="checkbox" />)}
                                              <span className="checkmark"></span>
                                            </label>
                                          </td>
                                          <td>{item.vendorCode}</td>
                                        </div>
                                      );
                                    case '':
                                      return (
                                        <div className='table__first-part'>
                                          <td>
                                            <label className="container">
                                              {isCheckedHeader ?
                                                (<input defaultChecked={true} type="checkbox" />)
                                                : (<input defaultChecked={false} type="checkbox" />)}
                                              <span className="checkmark"></span>
                                            </label>
                                          </td>
                                          <td>{item.category}</td>
                                        </div>
                                      );
                                    case '':
                                      return (
                                        <div className='table__first-part'>
                                          <td>
                                            <label className="container">
                                              {isCheckedHeader ?
                                                (<input defaultChecked={true} type="checkbox" />)
                                                : (<input defaultChecked={false} type="checkbox" />)}
                                              <span className="checkmark"></span>
                                            </label>
                                          </td>
                                          <td>{item.type}</td>
                                        </div>
                                      );
                                    case '..':
                                      return (
                                        <div className='table__first-part'>
                                          <td>
                                            <label className="container">
                                              {isCheckedHeader ?
                                                (<input defaultChecked={true} type="checkbox" />)
                                                : (<input defaultChecked={false} type="checkbox" />)}
                                              <span className="checkmark"></span>
                                            </label>
                                          </td>
                                          <td>{item.unitType}</td>
                                        </div>
                                      );
                                    case ' .':
                                      return (
                                        <div className='table__first-part'>
                                          <td>
                                            <label className="container">
                                              {isCheckedHeader ?
                                                (<input defaultChecked={true} type="checkbox" />)
                                                : (<input defaultChecked={false} type="checkbox" />)}
                                              <span className="checkmark"></span>
                                            </label>
                                          </td>
                                          <td>{item.perchasePrice}</td>
                                        </div>
                                      );
                                    case ' .':
                                      return (
                                        <div className='table__first-part'>
                                          <td>
                                            <label className="container">
                                              {isCheckedHeader ?
                                                (<input defaultChecked={true} type="checkbox" />)
                                                : (<input defaultChecked={false} type="checkbox" />)}
                                              <span className="checkmark"></span>
                                            </label>
                                          </td>
                                          <td>{item.salePrice}</td>
                                        </div>
                                      );
                                    case '':
                                      return (
                                        <div className='table__first-part'>
                                          <td>
                                            <label className="container">
                                              {isCheckedHeader ?
                                                (<input defaultChecked={true} type="checkbox" />)
                                                : (<input defaultChecked={false} type="checkbox" />)}
                                              <span className="checkmark"></span>
                                            </label>
                                          </td>
                                          <td>
                                            <span className={`table__item item-status item-status-${item.status}`}>
                                              <span className={`item-status__wrapper-${item.status}`}>
                                                {item.status === 'red' ? (
                                                  <>
                                                    
                                                    <span className='item-status__icon'>
                                                      <Cross />
                                                    </span>
                                                  </>
                                                ) : (
                                                  <>
                                                    
                                                  </>
                                                )}
                                              </span>
                                            </span>
                                          </td>
                                        </div>
                                      );

                                    default:
                                      break;
                                  }
                                }

                                switch (header) {
                                  case '':
                                    return (
                                      <td>{item.name}</td>
                                    );
                                  case '':
                                    return (
                                      <td>{item.vendorCode}</td>
                                    );
                                  case '':
                                    return (
                                      <td>{item.category}</td>
                                    );
                                  case '':
                                    return (
                                      <td>{item.type}</td>
                                    );
                                  case '..':
                                    return (
                                      <td>{item.unitType}</td>
                                    );
                                  case ' .':
                                    return (
                                      <td>{item.perchasePrice}</td>
                                    );
                                  case ' .':
                                    return (
                                      <td>{item.salePrice}</td>
                                    );
                                  case '':
                                    return (
                                      <div className='table__last-part'>
                                        <td>
                                          <span className={`table__item item-status item-status-${item.status}`}>
                                            <span className={`item-status__wrapper-${item.status}`}>
                                              {item.status === 'red' ? (
                                                <>
                                                  
                                                  <span className='item-status__icon'>
                                                    <Cross />
                                                  </span>
                                                </>
                                              ) : (
                                                <>
                                                  
                                                  <span className='item-status__icon'>
                                                    <ActualIcon />
                                                  </span>
                                                </>
                                              )}
                                            </span>
                                          </span>
                                        </td>
                                      </div>
                                    );

                                  default:
                                    break;
                                }
                              })}
                              <td><Actions /></td>
                            </>
                          </tr>
                        ))
                      )}
                    </>
                  )
                }

              </tbody>
            </SimpleBarReact>
          </table >
        )}

    </>
  );
}
